import React from 'react';
import {Row, Col, Form, Input, Modal, Button, message} from 'antd';
import {withRouter} from 'react-router-dom';
import {inject, observer} from 'mobx-react';
import * as mobx from 'mobx';

@Form.create()
@withRouter
@observer
class SendMailModal extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;

	}

	state = {
	}

	componentDidMount() {
	}

	closeModal = () =>{
		this.props.closeModal();
	}

	afterSend = ()=>{
		this.props.afterSend();
	}

	// 发送邮件
	sendBuildBusinessEmail = ()=>{
		this.props.form.validateFields((err, fieldsValue) => {
			if(err){
				return;
			}
			const {mailuri} = fieldsValue;
			const params = [mailuri];
			this.props.sendBuildBusinessEmail(params);
		})
	}

	sendBuildBusinessEmail2 = ()=>{
		const { businessCard } = this.store
		this.props.form.validateFields((err, fieldsValue) => {
			if(err){
				return;
			}
			const {mailuri} = fieldsValue;

			this.store.sendBuildBusinessEmail({'bc':businessCard, 'emails':[mailuri]}).then((res)=>{
				if(res){
                   this.afterSend();
				}
			})
		})
	}

	sendBuildBusinessEmail3 = () => {
		const {ratingplate} = this.store;
		this.props.form.validateFields((err, fieldsValue) => {
			if(err){
				return;
			}
			const {mailuri} = fieldsValue;
			this.store.sendNameplateEmail({'n':ratingplate, 'emails':[mailuri]}).then((res)=>{
				if(res){
					this.afterSend();
				}
			})
		})
	}

	trans = (type) => {
		switch (type) {
			case "businesscard":
				return this.sendBuildBusinessEmail2;
				break;
			case "ratingplate":
				return this.sendBuildBusinessEmail3;
				break;
			case "fivecards":
				return this.sendBuildBusinessEmail;
				break;
			default:
				return null;
				break;
		}
	}

	render() {
		const {visible, type} = this.props;

		const formItemLayout = {
			labelCol: {
				xs: { span: 24 },
				sm: { span: 24 },
			},
			wrapperCol: {
				xs: { span: 24 },
				sm: { span: 24 },
			},
		};
		const {getFieldDecorator} = this.props.form;
		return (
			<Modal
				visible={visible}
				title=""
				onCancel={this.closeModal}
				footer={null}
				width={940}
				bodyStyle={{textAlign:'center',height:'600px',verticalAlign:'middle'}}
			>
				<div style={{marginTop:'50px'}}><img
					width='257px'
					height="192px"
					src='./images/u294.gif'/>
				</div>
				<div style={{fontSize:'16px'}}>
					<Form {...formItemLayout}>
						<Form.Item label="" className="edit_form_bold">
							{getFieldDecorator('mailuri', {
								rules: [
									{
										required: true,
										type: 'email',
										message: '该项是必填并且符合邮箱命名规则',
									},
								],
							})(<div style={{marginTop:'35px',fontSize:'16px', textAlign:'center'}}>
								<span style={{color:'#000',fontSize:'14px',fontWeight:'400'}}>邮箱地址:</span>
								<Input maxLength={50}
									   style={{width:'360px',marginLeft:'5px'}}
									   placeholder="请输入(不超过50字)"
								/>
								<Button
								type="primary"
                                onClick={this.trans(type)}
								style={{marginLeft:'5px'}}
							    >发送</Button>
							</div>)}
						</Form.Item>
					</Form>
				</div>
				<div style={{textAlign:'left',paddingLeft:'30%'}}>
					<p style={{marginTop:'5px'}}>温馨提醒：发送时邮件将默认抄送申请人一封。</p>
					<p style={{marginTop:'15px'}}>常用地址：</p>
					<Row>
						<Col span={12}>
							<p>
								<span>地址：</span><span>广联达二期133美立特文印室 </span>
							</p>
							<p>
								<span>电话：</span><span>15234018584</span>
							</p>
							<p>
								<span>邮箱：</span><span>615053062@qq.com</span>
							</p>
						</Col>
						<Col span={12}>
							<p>
								<span>地址：</span><span>江阴市新城东九月广告图文</span>
							</p>
							<p>
								<span>电话：</span><span>15961679730</span>
							</p>
							<p>
								<span>邮箱：</span><span>27697090@qq.com</span>
							</p>
						</Col>
					</Row>
				</div>
			</Modal>
		)
	}
}

const WrappedSendMailModalForm = Form.create({})(SendMailModal);
export default WrappedSendMailModalForm;



