/*
* 大文件上传组件 antd upload 支持拖拽排序
* dangwei
* */
import React from "react";
import {Upload, Icon, message, Modal} from 'antd';
import oss from 'ali-oss';
import moment from 'moment';
import Viewer from 'react-viewer';
import Config from '@/config';
import {requestPost} from "@/utils/api";
import journalSchemas from "@/utils/Schemas/journal";
import {downloadFile, download} from "@/utils/download";
import bpmSchemas from "@/utils/Schemas/bpm";
import {inject, observer} from 'mobx-react';
import {arrayMove, SortableContainer, SortableElement, SortEnd} from 'react-sortable-hoc';
import UploadList from 'antd/es/upload/UploadList';
import './pictureGrid.css';

var ifcancel = false;
let abortCheckpoint;

const client = (self) => {
	const {token} = self.state
	// ali-oss v6.x版本的写法
	return new oss({
		accessKeyId: token.access_key_id,
		accessKeySecret: token.access_key_secret,
		stsToken: token.stsToken,
		region: token.region, //
		bucket: token.bucket,//
		endpoint: token.OSS_ENDPOINT,
		timeout: 5 * 60 * 1000
	});
};

const uploadPath = (path, file) => {
	// 上传文件的路径，使用日期命名文件目录
	return `${moment().format('YYYYMMDD')}/${file.uid}/${file.name}`
}

const UploadToOss = (self, path, file, option) => {
	const url = uploadPath(path, file)
	return new Promise((resolve, reject) => {
		client(self).multipartUpload(url, file, option).then(data => {
			resolve(data);
		}).catch(error => {
			reject(error)
		})
	})
}

const itemStyle = {
	width: 104,
	height: 104,
	margin: 4,
	cursor: 'grab',
	zIndex: '1000'
};
const SortableItem = SortableElement((params) => (
	<div style={itemStyle} className='affairs_upload'>
		<UploadList
			// locale={{previewFile: '预览', removeFile: '删除', downloadFile: "下载"}}
			locale={{previewFile: '预览', removeFile: '删除'}}
			listType={params.props.listType}
			onPreview={params.onPreview}
			onDownload={params.onDownload}
			onRemove={params.onRemove}
			items={[params.item]}
			showDownloadIcon={false}
			showPreviewIcon={false}

		/>
	</div>
));
const listStyle = {
	display: 'flex',
	flexWrap: 'wrap',
	maxWidth: '100%',
};
const SortableList = SortableContainer((params) => {
	return (
		<div style={listStyle}>
			{params.items.map((item, index) => (
				<SortableItem
					key={`${item.uid}`}
					index={index}
					item={item}
					props={params.props}
					onPreview={params.props.onPreview}
					onDownload={params.props.onDownload}
					onRemove={params.props.onRemove}
				/>
			))}
			<Upload
				{...params.props}
				className="affairs_upload_normal"
				showUploadList={false}
			>
				{params.uploadButton}
			</Upload>
			{params.cancelButton}
		</div>
	);
});


@inject('appStore')
@observer
class MyUpload4Affairs extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.appStore;
		this.state = {
			fileList: this.props.fileList,
			loading: false,
			token: {
				access_key_id: '', // oss的key_id
				access_key_secret: '', // oss的secret
				stsToken: '',
				region: 'oss-cn-beijing', // oss的region
				bucket: 'xinlj', // oss的bucket
				OSS_ENDPOINT: 'oss-cn-beijing.aliyuncs.com',  // 自己oss服务器的配置信息
			},
			host: 'https://xinlj.oss-cn-beijing.aliyuncs.com/', // 阿里云host
			previewVisible: false,
			previewImage: '',
			name: "",
			filePath: "",
			visible: false,
			imageArray: [], // 图片预览
			activeIndex: 0,
			expiration: "",  // 过期时间
		};
	}

	componentWillReceiveProps(nextProps) {
		if (JSON.stringify(nextProps.fileList) != JSON.stringify(this.props.fileList)) {
			this.setState({
				fileList: nextProps.fileList.map((file) => {
					if (/\.(doc|docx)$/.test(file.name)) {
						file.url = './images/word_default.png'
					} else if (/\.(xls|xlsx)$/.test(file.name)) {
						file.url = './images/exel_default.png'
					} else if (/\.(pdf)$/.test(file.name)) {
						file.url = './images/pdf.svg'
					} else if (/\.(zip|rar)$/.test(file.name)) {
						file.url = './images/zip_default.png'
					} else if (/\.(ppt|pptx)$/.test(file.name)) {
						file.url = './images/ppt.png'
					} else {
						file.url = file.url + '?x-oss-process=image/resize,w_500,limit_0'
					}
					return file;
				})
			});
		}
		if (JSON.stringify(nextProps.ossData) != JSON.stringify(this.props.ossData)) {
			let token = JSON.parse(JSON.stringify(this.state.token));
			this.setState({
				token: Object.assign(token, {
					access_key_id: nextProps.ossData.accessKeyId, // oss的key_id
					access_key_secret: nextProps.ossData.accessKeySecret, // oss的secret
					stsToken: nextProps.ossData.securityToken
				})
			});
		}
	}

	componentDidMount() {
		if (this.props.journalType) {
			this.store.journal = false; // 记录一次
		}
		if (this.props.fileList) {
			this.setState({
				fileList: this.props.fileList.map((file) => {
					if (/\.(doc|docx)$/.test(file.name)) {
						file.url = './images/word_default.png'
					} else if (/\.(xls|xlsx)$/.test(file.name)) {
						file.url = './images/exel_default.png'
					} else if (/\.(pdf)$/.test(file.name)) {
						file.url = './images/pdf.svg'
					} else if (/\.(zip|rar)$/.test(file.name)) {
						file.url = './images/zip_default.png'
					} else if (/\.(ppt|pptx)$/.test(file.name)) {
						file.url = './images/ppt.png'
					} else {
						file.url = file.url + '?x-oss-process=image/resize,w_500,limit_0'
					}
					return file;
				})
			});
		}

		// 访问后端，获取请求上传凭证
        this.init();
	}

	init = () => {
         // 阿里云上传参数
		requestPost('getSts', Config.bpm.getSts, bpmSchemas.bpm.getSts, {})
		.then((res) => {
			let str = typeof res == "string" ? JSON.parse(res) : res;
			if (str) {
				let token = JSON.parse(JSON.stringify(this.state.token));
				this.setState({
					token: Object.assign(token, {
						access_key_id: str.accessKeyId,
						access_key_secret: str.accessKeySecret,
						stsToken: str.securityToken
					}),
					expiration: str.expiration
				});
			}
		});
	}

	// 取消阿里云上传
	cancelUploadFileToAliyun = (params) => {
		requestPost('cancelUploadFileToAliyun', Config.journal.cancelUploadFileToAliyun, journalSchemas.journal.cancelUploadFileToAliyun,
			{uploadId: params.uploadId, key: params.name})
			.then((res) => {
                if (res==true) {
                   message.success("取消上传成功");
                }
			});
	}

	getDocumentUrl = (url) => {
		return requestPost('getDocumentUrl', Config.journal.getDocumentUrl, journalSchemas.journal.getDocumentUrl, {url: url})
	}

	// 更新url
	changeSrc = (res, file) => {
		const {fileList, host} = this.state;
		let oldFileList = JSON.parse(JSON.stringify(fileList));

		const url = host + uploadPath('path', file);
		let obj = Object.assign({}, {
			uid: file.uid,
			name: file.name,
			status: "done",
			url: url,
			fileId: null,
			fileName: file.name,
			filePath: url,
			id: "",
		});
		let newFileList = [obj];

		// 处理附件
		let array = JSON.parse(JSON.stringify([...oldFileList, ...newFileList]))
		this.props.saveAttachment(array);
	}

	beforeUpload = (file) => {

		// 上传凭证失效，重查
		const {expiration} = this.state;
		const expire = new Date(expiration).getTime();
		if (expiration!=""&&expire < Date.now()) {
			this.init();
		}

		const {accept} = this.props;
		const acceptDefault = ".jpg,.jpeg,.gif,.png,.bmp,.doc,.docx,.xlsx,.xls,.pdf,.ppt,.pptx";  // 默认值限制格式
		if (accept == undefined) {  // 没有传参数
			const type = file.type.split("/")[1];
			if (!acceptDefault.includes(type)) {
				//message.warn("格式不对");
				//return false;
			}
		} else {   // 外部传参数
			const type = file.type.split("/")[1];
			if (!accept.includes(type)) {
				message.warn("格式不对, 只允许上传图片");
				return false
			}
		}

		const isLt2M = file.size / 1024 / 1024 < 2000;
		if (!isLt2M) {
			message.error('Image must smaller than 2MB!');
		}

		this.setState({
			loading: true
		}, () => {
			if (this.props.journalType) {
				this.store.journal = true; // 记录一次
			}
		});

		let reader = new FileReader();
		reader.readAsDataURL(file);
		reader.onloadend = () => {
			// 使用ossupload覆盖默认的上传方法
			UploadToOss(this, '上传路径oss配置信息', file, {
				parallel: 4,
				partSize: 1024 * 1024,
				progress: function (p, cpt, res) {
					abortCheckpoint = cpt;
					//console.log(p, cpt, res, '进度');
				}
			})
			.then(data => {
				return data;
			})
			.then(data => {
				// 处理地址
				const {res} = data;
				if (res.status && res.status == 200) {  // 单个文件上传成功
					this.setState({
						loading: false
					}, () => {
						if (this.props.journalType) {
							this.store.journal = false; // 记录一次
						}
					});

					this.changeSrc(res, file);
				}
			}).catch((error) => {

				if (error.name === 'abort') {
					// handle abort

				} else {
					//message.error("文件上传失败");
					this.setState({
						loading: false
					}, () => {
						if (this.props.journalType) {
							this.store.journal = false; // 记录一次
						}
					});
					return false;
				}
			});
		}

		return false; // 不调用默认的上传方法
	}

	handlePreview = async file => {
		// 文档
		if (/\.(doc|docx)$/.test(file.name) || /\.(xls|xlsx)$/.test(file.name) || /\.(pdf)$/.test(file.name) || /\.(zip|rar)$/.test(file.name) || /\.(ppt|pptx)$/.test(file.name)) {
			this.getDocumentUrl(file.filePath).then((res) => {
				this.setState({
					previewImage: res,
					previewVisible: true,
					name: file.name,
					filePath: file.filePath
				});
			});
		} else {  // 图片、音频
			// 文件类型判断
			function isAssetTypeAnImage(ext) {
				return [
					'png', 'jpg', 'jpeg', 'bmp', 'gif', 'webp', 'psd', 'svg', 'tiff'].indexOf(ext.toLowerCase()) !== -1;
			}

			//获取后缀
			function getExt(filePath) {
				//获取最后一个.的位置
				let index = filePath.lastIndexOf(".");
				//获取后缀
				let ext = filePath.substr(index + 1);
				return ext;
			}

			if (isAssetTypeAnImage(getExt(file.filePath))) {  // 判断是图片
				const fileList = JSON.parse(JSON.stringify(this.state.fileList));
				let imageArray;
				imageArray = fileList.filter((item) => isAssetTypeAnImage(getExt(item.filePath))).map((item) => {
					return Object.assign({}, {
						src: item.filePath,
						alt: item.name,
						downloadUrl: item.filePath
					})
				});
				this.setState({
					visible: true,
					imageArray: Object.assign([], imageArray),
					activeIndex: imageArray.findIndex((item) => item.src == file.filePath)
				});
			} else {   // 音频
				this.setState({
					previewImage: file.filePath,
					previewVisible: true,
					name: file.name,
					filePath: file.filePath
				});
			}
		}
	};

	handleCancel = (type) => this.setState({[type]: false});

	// 手动下载
	downloadUrl = () => {
		const {filePath, name} = this.state;
		const isImage = (/\.(doc|docx)$/.test(name) || /\.(xls|xlsx)$/.test(name) || /\.(pdf)$/.test(name) || /\.(zip|rar)$/.test(name)) || (/\.(ppt|pptx)$/.test(name));
		if (isImage) {
			downloadFile(filePath, name);  // 直接下载pdf
		} else {
			this.downloadImage(filePath, name);
		}
	}

	// 预览下载
	onDownload = (file) => {
		const {filePath, name} = file;
		const isImage = (/\.(doc|docx)$/.test(name) || /\.(xls|xlsx)$/.test(name) || /\.(pdf)$/.test(name) || /\.(zip|rar)$/.test(name)) || (/\.(ppt|pptx)$/.test(name));
		if (isImage) {
			downloadFile(filePath, name);  // 直接下载pdf
		} else {
			this.downloadImage(filePath, name);
		}
	}

	// 直接下载
	downloadImage = (filePath, name) => {
		//获取最后一个.的位置
		var index = filePath.lastIndexOf(".");
		//获取后缀
		var ext = filePath.substr(index + 1);

		// 直接下载图片
		var x = new XMLHttpRequest();
		x.open("GET", filePath, true);
		x.responseType = 'blob';
		x.onload = function (e) {
			download(x.response, name, "image/" + ext);
		}
		x.send();
	}

	// 取消上传
	cancel = () => {
		ifcancel = true;
		const {uploadId, name} = abortCheckpoint;
		client(this).cancel();
		if (abortCheckpoint&&uploadId!=undefined) {
			this.cancelUploadFileToAliyun({name, uploadId});
		}
		this.setState({
			loading: false,
		}, ()=>{
			if (this.props.journalType) {
				this.store.journal = false; // 清楚记录
			}
		});
	}

	onSortEnd = ({oldIndex, newIndex}: SortEnd) => {
		const {fileList} = this.state;
		this.props.saveAttachment(arrayMove(fileList, oldIndex, newIndex));
	};

	onRemove = (file) => {
		const {fileList} = this.state;
		const newFileList = fileList.filter(
			(item) => item.uid !== file.uid
		);
		this.props.saveAttachment(newFileList);
	}

	render() {
		const {previewVisible, previewImage, fileList, name, loading, visible, imageArray, activeIndex} = this.state;
		const {multiple, disabled, accept} = this.props;
		const acceptDefault = ".jpg,.jpeg,.gif,.png,.bmp,.doc,.docx,.xlsx,.xls,.pdf,.ppt,.pptx";  // 默认值限制格式
		const props = {
			accept: accept == undefined ? acceptDefault : accept,
			name: 'file',
			multiple: multiple,
			beforeUpload: this.beforeUpload,
			disabled: disabled,
			listType: "picture-card",
			onPreview: this.handlePreview,
			onDownload: this.onDownload,
			onRemove: this.onRemove,

		};
		const uploadButton = (
			<div className="pr">
				<Icon type={loading ? "loading" : "plus"}/>
				{loading ? <div className="ant-upload-text">上传中</div> : null}
			</div>
		);
		const cancelButton = (
			loading ? <Icon type="stop"
			                 title="取消上传"
			                 style={{
				                 fontSize: '15px',
				                 color: '#1890ff'
			                 }}
			                onClick={this.cancel} /> : null
		);
		const isImage = (/\.(doc|docx)$/.test(name) || /\.(xls|xlsx)$/.test(name) || /\.(pdf)$/.test(name) || /\.(zip|rar)$/.test(name)) || (/\.(ppt|pptx)$/.test(name));
		return (
			<div>
				<SortableList
					distance={1}
					items={fileList}
					onSortEnd={this.onSortEnd}
					axis="xy"
					helperClass="SortableHelper"
					props={props}
					uploadButton={disabled ? "" : uploadButton}
					cancelButton={cancelButton}
				/>
				<Modal
					title={<div>{name}
						<a href="javascript:;" onClick={this.downloadUrl}>下载</a>
					</div>}
					width="50%"
					style={{minHeight: '450px'}}
					visible={previewVisible}
					footer={null}
					onCancel={this.handleCancel.bind(this, 'previewVisible')}>
					{isImage ? <iframe
							src={previewImage}
							width='100%'
							height='100%'
							style={{minHeight: '450px'}}
							frameBorder='1'>
						</iframe> :
						<img style={{backgroundSize: 'conver', width: '100%', maxHeight: '400px'}} src={previewImage}
						     alt=""/>}
				</Modal>
				<Viewer
					visible={visible}
					onClose={this.handleCancel.bind(this, 'visible')}
					images={imageArray}
					activeIndex={activeIndex}
					downloadable={true}
					customToolbar={(toolbars) => {
						return toolbars.filter(item => item.key !== "download").concat([
							{
								key: "download",
								render: (<div><i className="react-viewer-icon react-viewer-icon-download"></i></div>),
								onClick: (activeImage) => {
									const {src, alt} = activeImage;
									this.downloadImage(src, alt);
								},
							},
						]);
					}}
				/>
			</div>
		);
	}
}

export default MyUpload4Affairs;



